import React from "react";
import { Container, Row } from "react-bootstrap";
import Card from "react-bootstrap/Card";
import CopyToClipboard from "react-copy-to-clipboard";
import Button from "react-bootstrap/Button";
import Particle from "../Particle";
import "react-pdf/dist/esm/Page/AnnotationLayer.css";

function ImportExamples() {

  const Examples = [
    { 
      title: "5 Bodies Stable Orbit",
      description: "This is a simulation with a large center fixed body and 4 other bodies orbiting on a similar plane.",
      json: "{Aur:[px:-1.75685,py:2.432484,pz:1.691745,vx:-0.02685453,vy:0.03829779,vz:0.02639658,m:1E+09,s:8,glow:False,];Eln:[px:-9.117545,py:-3.180285,pz:1.170338,vx:3.170671,vy:-4.859745,vz:-3.279314,m:5000000,s:4.5,glow:False,];Eos:[px:11.3917,py:-12.8841,pz:1.73417,vx:3.125518,vy:2.481343,vz:-0.0979283,m:100,s:3,glow:False,];Body 4:[px:-8.544214,py:-23.5379,pz:0.9040414,vx:3.111023,vy:-0.9085627,vz:-0.3383135,m:1E-07,s:1,glow:False,];Body 5:[px:1.286137,py:20.34493,pz:2.479509,vx:-3.643719,vy:0.4344996,vz:0.805969,m:1E-07,s:4.61054,glow:False,];}end"
    },
    {
      title: "Comet Orbit",
      description: "A simple circular orbit example with a comet.",
      json: "{Aur:[px:0,py:0,pz:0,vx:0,vy:0,vz:0,m:1E+09,s:8,glow:False,];Eos:[px:15,py:0,pz:0,vx:0,vy:4.5,vz:0,m:999.9998,s:3,glow:False,];Ipo:[px:-25,py:0,pz:0,vx:0,vy:-3,vz:0,m:999.9999,s:3,glow:False,];}end"
    },
    {
      title: "3 Bodies 2 Different Planes of Orbit",
      description: "The same as the 2 circular orbit example in the app, but with both orbits on perpendicular planes.",
      json: "{Aur:[px:0,py:0,pz:0,vx:0,vy:0,vz:0,m:1E+09,s:8,glow:False,];Eos:[px:15,py:0,pz:0,vx:0,vy:4.5,vz:0,m:999.9999,s:3,glow:False,];Ipo:[px:-15,py:0,pz:0,vx:0,vy:0,vz:-4.5,m:999.9999,s:3,glow:False,];}end"
    }
  ]

  return (
    <div>
      <Container fluid className="ImportExamples-section">
        <Particle />
        <Row style={{ justifyContent: "center", position: "relative", top: 100}}>
          <Card className="custom-class" border="warning">
            <Card.Body >
              <Card.Title style={{ color: "orange" }}>Copy the examples below and import them into the app to see them run!</Card.Title>
            </Card.Body>
          </Card>
        </Row>
        {Examples.map((example) => (
        <Row style={{ justifyContent: "center", position: "relative", top: 100}}>
          <Card className="custom-class" border="secondary">
            {example.img && <Card.Img variant="top" src={example.img} />}
            <Card.Body >
              <Card.Title style={{ color: "white" }}>{example.title}</Card.Title>
              <Card.Text style={{ color: "white" }}>{example.description}</Card.Text>
              <CopyToClipboard text={example.json}>
                <Button>Copy to Clipboard</Button>
              </CopyToClipboard>
            </Card.Body>
          </Card>
        </Row>
        ))}
        </Container>
    </div>
  );
}

export default ImportExamples;
