import React, { useState, useEffect } from "react";
import { Container, Row } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Particle from "../Particle";
import elementary1pdf from "../../Assets/Orbit-Learn-Class-Activity.pdf";
import elementary2pdf from "../../Assets/Orbit-Learn-Lesson-Plan.pdf";
import middleSchool1pdf from "../../Assets/Gravity-Worksheet.pdf";
import middleSchool2pdf from "../../Assets/Orbit-Learn-Group-Project.pdf";
import high1pdf from "../../Assets/7-Body-Stable-Homework.pdf";
import high2pdf from "../../Assets/Conservation-Of-Angular-Momentum-Homework.pdf";
import { AiOutlineDownload } from "react-icons/ai";
import { Document, Page, pdfjs } from "react-pdf";
import "react-pdf/dist/esm/Page/AnnotationLayer.css";
import Collapsible from "../Collapsible";
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;


function WorksheetNew() {
  const [width, setWidth] = useState(1200);

  useEffect(() => {
    setWidth(window.innerWidth);
  }, []);

  return (
    <div>
      <Container fluid className="Worksheet-section">
        <Particle />
        <Row style={{ justifyContent: "center", position: "relative", top: 100}}>
        <Collapsible title='ELEMENTARY SCHOOL' extendedTitle={true}>
            <Collapsible title='Orbit Learn Class Activity' isSub={true}>
              <Row style={{ justifyContent: "center", position: "relative", top: 0}}>
                <Button variant="primary" href={elementary1pdf} target="_blank">
                  <AiOutlineDownload /> &nbsp;Download PDF
                </Button>
              </Row>
              <Row style={{ justifyContent: "center", position: "relative", top: 10, marginBottom: 20}} className="Worksheet">
                <Document file={elementary1pdf}>
                  <Page pageNumber={1} scale={width > 786 ? 1.7 : 0.6} />
                </Document>
              </Row>
            </Collapsible>
            <Collapsible title='Orbit Learn Lesson Plan' isSub={true}>
              <Row style={{ justifyContent: "center", position: "relative", top: 0}}>
                <Button variant="primary" href={elementary2pdf} target="_blank">
                  <AiOutlineDownload /> &nbsp;Download PDF
                </Button>
              </Row>
              <Row style={{ justifyContent: "center", position: "relative", top: 10, marginBottom: 20}} className="Worksheet">
                <Document file={elementary2pdf}>
                  <Page pageNumber={1} scale={width > 786 ? 1.7 : 0.6} />
                </Document>
              </Row>
            </Collapsible>
          </Collapsible>
          </Row>

          <Row style={{ justifyContent: "center", position: "relative", top: 100}}>
          <Collapsible title='MIDDLE SCHOOL' extendedTitle={true}>
            <Collapsible title='Gravity Worksheet' isSub={true}>
              <Row style={{ justifyContent: "center", position: "relative", top: 0}}>
                <Button variant="primary" href={middleSchool1pdf} target="_blank">
                  <AiOutlineDownload /> &nbsp;Download PDF
                </Button>
              </Row>
              <Row style={{ justifyContent: "center", position: "relative", top: 10}} className="Worksheet">
                <Document file={middleSchool1pdf}>
                  <Page pageNumber={1} scale={width > 786 ? 1.7 : 0.6} />
                </Document>
              </Row>
              <Row style={{ justifyContent: "center", position: "relative", top: 20, marginBottom: 20}} className="Worksheet">
                <Document file={middleSchool1pdf}>
                  <Page pageNumber={2} scale={width > 786 ? 1.7 : 0.6} />
                </Document>
              </Row>
            </Collapsible>
            <Collapsible title='Orbit Learn Group Project' isSub={true}>
              <Row style={{ justifyContent: "center", position: "relative", top: 0}}>
                <Button variant="primary" href={middleSchool2pdf} target="_blank">
                  <AiOutlineDownload /> &nbsp;Download PDF
                </Button>
              </Row>
              <Row style={{ justifyContent: "center", position: "relative", top: 10, marginBottom: 20}} className="Worksheet">
                <Document file={middleSchool2pdf}>
                  <Page pageNumber={1} scale={width > 786 ? 1.7 : 0.6} />
                </Document>
              </Row>
            </Collapsible>
          </Collapsible>
          </Row>

          <Row style={{ justifyContent: "center", position: "relative", top: 100}}>
          <Collapsible title='HIGH SCHOOL' extendedTitle={true}>
          <Collapsible title='7 Body Stable Homework' isSub={true}>
              <Row style={{ justifyContent: "center", position: "relative", top: 0}}>
                <Button variant="primary" href={high1pdf} target="_blank">
                  <AiOutlineDownload /> &nbsp;Download PDF
                </Button>
              </Row>
              <Row style={{ justifyContent: "center", position: "relative", top: 10, marginBottom: 20}} className="Worksheet">
                <Document file={high1pdf}>
                  <Page pageNumber={1} scale={width > 786 ? 1.7 : 0.6} />
                </Document>
              </Row>
            </Collapsible>
            <Collapsible title='Conservation of Angular Momentum Homework' isSub={true}>
              <Row style={{ justifyContent: "center", position: "relative", top: 0}}>
                <Button variant="primary" href={high2pdf} target="_blank">
                  <AiOutlineDownload /> &nbsp;Download PDF
                </Button>
              </Row>
              <Row style={{ justifyContent: "center", position: "relative", top: 10, marginBottom: 20}} className="Worksheet">
                <Document file={high2pdf}>
                  <Page pageNumber={1} scale={width > 786 ? 1.7 : 0.6} />
                </Document>
              </Row>
            </Collapsible>
        </Collapsible>  
        </Row>
      </Container>
    </div>
  );
}

export default WorksheetNew;
